import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Picture } from "common/components/Picture";
import { getAnalyticsId } from "common/utils/AnalyticsId";
import { Text } from "common/components/Text";
import { Heading, HeadingTag } from "common/components/new/Heading";
import { Button, ButtonSize } from "common/components/new/Button";
import { Container } from "common/components/Container";

import { PriceRow } from "./components/PriceRow";
import { PriceProps } from "./Price.types";
import styles from "./Price.module.scss";

const cx = classNames.bind(styles);

export const Price: FunctionComponent<PriceProps> = ({
    legal,
    offer,
    title,
    button,
    className,
    pricesList,
    description,
    image,
    version,
}) => {
    return (
        <section className={cx("root", className)}>
            <Container className={cx("container")}>
                <div className={cx("row")}>
                    {image && (
                        <Picture
                            {...image}
                            className={cx("column-poster")}
                            classNameImg={cx("column-poster-img")}
                        />
                    )}
                    <div className={cx("column-content")}>
                        <div className={cx("header")}>
                            <Heading
                                tag={HeadingTag.H2}
                                className={cx("title")}
                            >
                                {title}
                            </Heading>
                            {description && (
                                <Text className={cx("description")}>
                                    {description}
                                </Text>
                            )}
                        </div>
                        <div className={cx("prices")}>
                            {pricesList.map((item) => {
                                return (
                                    <PriceRow
                                        {...item}
                                        version={version}
                                        key={item.name}
                                    />
                                );
                            })}
                        </div>
                        <div className={cx("footer")}>
                            <div className={cx("button-container")}>
                                <Button
                                    onClick={button.onClick}
                                    className={cx("button")}
                                    size={ButtonSize.M}
                                    id={getAnalyticsId({
                                        zone: "price",
                                        label: "submit",
                                    })}
                                >
                                    {button.name}
                                </Button>
                                <Text className={cx("offer")}>{offer}</Text>
                            </div>
                            <Text className={cx("legal")}>{legal}</Text>
                        </div>
                    </div>
                </div>
            </Container>
        </section>
    );
};
