import { FC, useEffect, useRef } from "react";
import classNames from "classnames/bind";

import { CountryVersion, PageTheme } from "common/constants/enum";
import { Head } from "common/components/new/Head";
import { Container } from "common/components/Container";
import { OurResults } from "common/components/new/OurResults";
import { NewAppointment } from "common/components/NewAppointment";
import { TableSolutions } from "common/components/new/TableSolutions";
import { MaxWidthContainer } from "common/components/new/MaxWidthContainer";
import { AdvantagesBlock } from "common/components/AdvantagesBlock";
import { useComputedFixedButtonOffset } from "common/hooks/useComputedFixedButtonOffset";
import { useFixedButtonContext } from "common/context/fixedButtonContext";
import { Heading } from "common/components/new/Heading";

import { Price } from "components/new/Price";
import { Footer } from "components/Footer";
import { Orthodontics } from "components/new/Orthodontics";

import { GoogleAdsProps } from "./GoogleAdsLayout.types";
import styles from "./GoogleAdsLayout.module.scss";

const cx = classNames.bind(styles);

export const GoogleAdsLayout: FC<GoogleAdsProps> = ({
    content,
    version,
    language,
}) => {
    const {
        price,
        title,
        button,
        results,
        contacts,
        solutions,
        advantages,
        orthodontics,
        bookingFormBlock,
    } = content;

    const appointmentRef = useRef<HTMLDivElement>(null);

    const {
        isFixedButtonActive,
        scrollToAppointment,
    } = useComputedFixedButtonOffset(appointmentRef);

    const { setFixedButton } = useFixedButtonContext();

    useEffect(() => {
        setFixedButton(isFixedButtonActive);
    }, [isFixedButtonActive]);

    const handleScrollToAppointment = () => {
        scrollToAppointment();
    };

    return (
        <MaxWidthContainer className={cx("root")}>
            <Head
                contacts={contacts}
                button={{
                    name: button.name,
                    onClick: handleScrollToAppointment,
                }}
                isFixedButtonActive={isFixedButtonActive}
                title={title}
                className={cx("head")}
            />
            <div ref={appointmentRef}>
                <Container>
                    <Heading
                        className={cx("appointment-title", {
                            "appointment-title_ss-pro":
                                version === CountryVersion.GERMANY,
                            "appointment-title_germany":
                                version === CountryVersion.GERMANY,
                        })}
                    >
                        {bookingFormBlock.title}
                    </Heading>
                    <NewAppointment
                        version={version}
                        language={language}
                        className={cx("appointment")}
                    />
                </Container>
            </div>
            <div className={cx("container_with_mb")}>
                <Orthodontics {...orthodontics} />
            </div>
            <div className={cx("table")}>
                <Container>
                    <TableSolutions {...solutions} />
                </Container>
            </div>
            <Price
                {...price}
                version={version}
                button={{
                    name: button.name,
                    onClick: handleScrollToAppointment,
                }}
                className={cx("price")}
            />
            <div className={cx("container_with_mb")}>
                <Container>
                    <AdvantagesBlock {...advantages} theme={PageTheme.WHITE} />
                </Container>
            </div>
            <div className={cx("results")}>
                <Container>
                    <OurResults {...results} />
                </Container>
            </div>
            <Container>
                <Footer {...contacts} />
            </Container>
        </MaxWidthContainer>
    );
};
