import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Text } from "common/components/Text";
import { PaymentMonthly } from "common/components/new/PriceBlock/components/PaymentMonthly";
import { PaymentOnce } from "common/components/new/PriceBlock/components/PaymentOnce";

import { PriceRowProps } from "./PriceRow.types";
import styles from "./PriceRow.module.scss";

const cx = classNames.bind(styles);

export const PriceRow: FunctionComponent<PriceRowProps> = ({
    name,
    paymentMonthly,
    paymentOnce,
    className,
    prepayment,
    version,
}) => {
    return (
        <div className={cx("root", className)}>
            <Heading
                type={HeadingType.S}
                tag={HeadingTag.H3}
                className={cx("name")}
            >
                {name}
            </Heading>
            <div>
                <div className={cx("amount")}>
                    {paymentMonthly && (
                        <Heading tag={HeadingTag.H2} className={cx("price")}>
                            <PaymentMonthly
                                className={cx("value")}
                                payment={paymentMonthly.payment}
                                month={paymentMonthly.month}
                                period={paymentMonthly.period}
                                version={version}
                                currency={paymentMonthly.currency}
                            />
                        </Heading>
                    )}
                    {paymentOnce && (
                        <Heading tag={HeadingTag.H2} className={cx("price")}>
                            <PaymentOnce
                                payment={paymentOnce.payment}
                                version={version}
                                currency={paymentOnce.currency}
                            />
                        </Heading>
                    )}
                </div>
                {prepayment && (
                    <Text className={cx("prepayment")}>{prepayment}</Text>
                )}
            </div>
        </div>
    );
};
