import React, { Fragment, FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Heading, HeadingTag } from "common/components/new/Heading";
import { Icon } from "common/components/Icons";
import DoneIcon from "common/components/Icons/icons/24/done.svg";
import CircleIcon from "common/components/Icons/icons/24/circle.svg";

import {
    TableSolutionsProps,
    ColumnsProps,
    DataSourceProps,
} from "./TableSolutions.types";
import styles from "./TableSolutions.module.scss";

const cx = classNames.bind(styles);

const getSvg = (isChecked: boolean) => {
    if (isChecked) {
        return (
            <Icon className={cx("done-icon")}>
                <DoneIcon />
            </Icon>
        );
    }

    return (
        <Icon className={cx("circle-icon")}>
            <CircleIcon />
        </Icon>
    );
};

const renderColumn = (column: ColumnsProps) => {
    const { dataIndex, title } = column;

    let content: React.ReactNode = title;

    if (dataIndex === "company1") {
        content = (
            <div className={cx("marked-title")} key={dataIndex}>
                {title}
            </div>
        );
    }

    return (
        <th
            key={dataIndex}
            className={cx("th", "th_thead", {
                th_marked: dataIndex === "company1",
            })}
        >
            {content}
        </th>
    );
};

const renderRow = (row: DataSourceProps, index: number) => {
    const { service, company1, company2, company3, company4 } = row;

    return (
        <Fragment key={index}>
            <tr className={cx("tr", "tr_tbody", "tr_mobile")}>
                <td colSpan={4} className={cx("td", "td_tbody")}>
                    {service}
                </td>
            </tr>
            <tr className={cx("tr", "tr_tbody")}>
                <td className={cx("td", "td_tbody")}>{service}</td>
                <td
                    className={cx("td", "td_tbody", "td_marked", {
                        td_unchecked: !company1,
                    })}
                >
                    {getSvg(company1)}
                </td>
                <td
                    className={cx("td", "td_tbody", {
                        td_unchecked: !company2,
                    })}
                >
                    {getSvg(company2)}
                </td>
                <td
                    className={cx("td", "td_tbody", {
                        td_unchecked: !company3,
                    })}
                >
                    {getSvg(company3)}
                </td>
                <td
                    className={cx("td", "td_tbody", {
                        td_unchecked: !company4,
                    })}
                >
                    {getSvg(company4)}
                </td>
            </tr>
        </Fragment>
    );
};

export const TableSolutions: FunctionComponent<TableSolutionsProps> = ({
    title,
    table,
    className,
}) => {
    const { columns = [], data = [] } = table || {};

    return (
        <div className={cx("root", className)}>
            {title && (
                <Heading className={cx("title")} tag={HeadingTag.H2}>
                    {title}
                </Heading>
            )}

            <table className={cx("table")}>
                <thead className={cx("thead")}>
                    <tr className={cx("tr", "tr_thead")}>
                        {columns.map(renderColumn)}
                    </tr>
                </thead>

                <tbody className={cx("tbody")}>{data.map(renderRow)}</tbody>
            </table>
        </div>
    );
};
