import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Icon } from "common/components/Icons";
import { Text } from "common/components/Text";

import { TextWithIconProps } from "./TextWithIcon.types";
import styles from "./TextWithIcon.module.scss";

const cx = classNames.bind(styles);

export const TextWithIcon: FunctionComponent<TextWithIconProps> = ({
    icon,
    content,
    className,
}) => {
    return (
        <div className={cx("root", className)}>
            <Icon className={cx("icon")}>{icon}</Icon>
            <Text className={cx("content")}>{content}</Text>
        </div>
    );
};
