import { FC } from "react";
import classNames from "classnames/bind";

import { Picture } from "common/components/Picture";

import { PostersProps } from "./Posters.types";
import styles from "../../Orthodontics.module.scss";

const cx = classNames.bind(styles);

export const Posters: FC<PostersProps> = ({ images }) => {
    return (
        <div
            className={cx("column_posters", {
                column_posters_one_img: images?.length === 1,
            })}
        >
            {images?.map((image, index) => (
                <Picture
                    {...image}
                    key={index}
                    classNameImg={cx("poster", {
                        one_poster: images.length === 1,
                        ["poster_with-fix-height"]:
                            images.length > 0 && index === images.length - 1,
                    })}
                />
            ))}
        </div>
    );
};
