import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { getAnalyticsId } from "common/utils/AnalyticsId";
import { Text } from "common/components/Text";
import { Button } from "common/components/new/Button";
import { Container } from "common/components/Container";
import { Heading, HeadingTag } from "common/components/new/Heading";

import { TextWithIconList } from "components/new/TextWithIconList";

import { Posters } from "./components/Posters";
import { OrthodonticsProps } from "./Orthodontics.types";
import styles from "./Orthodontics.module.scss";

const cx = classNames.bind(styles);

export const Orthodontics: FunctionComponent<OrthodonticsProps> = (props) => {
    const {
        list,
        title,
        button,
        className,
        description,
        images,
        onButtonClick,
    } = props;

    return (
        <section className={cx("root", className)}>
            <Container className={cx("container")}>
                <div className={cx("row")}>
                    <div
                        className={cx("column_content", {
                            column_content_one_img: images?.length === 1,
                        })}
                    >
                        <div className={cx("content")}>
                            <div className={cx("header")}>
                                <Heading
                                    tag={HeadingTag.H2}
                                    className={cx("title")}
                                >
                                    {title}
                                </Heading>
                                <Text className={cx("description")}>
                                    {description}
                                </Text>
                            </div>

                            <TextWithIconList
                                list={list}
                                className={cx("list", {
                                    list_without_pb: !button,
                                })}
                                classNameItem={cx("point")}
                            />
                            {button && (
                                <Button
                                    {...button}
                                    id={getAnalyticsId({
                                        zone: "orthodontics",
                                    })}
                                    className={cx("button")}
                                    onClick={onButtonClick}
                                />
                            )}
                        </div>
                    </div>

                    {images && images?.length > 0 && (
                        <Posters images={images} />
                    )}
                </div>
            </Container>
        </section>
    );
};
