import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import EasyIcon from "common/components/Icons/icons/48/easy.svg";
import FollowUpIcon from "common/components/Icons/icons/48/follow-up.svg";
import ChangeIcon from "common/components/Icons/icons/48/change.svg";
import Calendar6Icon from "common/components/Icons/icons/48/calendar-6.svg";

import { TextWithIcon } from "components/new/TextWithIcon";

import { TextWithIconListProps } from "./TextWithIconList.types";
import styles from "./TextWithIconList.module.scss";

const cx = classNames.bind(styles);

export const TextWithIconList: FunctionComponent<TextWithIconListProps> = ({
    list = [],
    className,
    classNameItem,
}) => {
    const renderIcon = (iconName: string) => {
        switch (iconName) {
            case "easy48":
                return <EasyIcon />;
            case "change48":
                return <ChangeIcon />;
            case "followUp48":
                return <FollowUpIcon />;
            default:
                return <Calendar6Icon />;
        }
    };

    return (
        <div className={cx("root", className)}>
            {list.map((item, index) => {
                return (
                    <TextWithIcon
                        {...item}
                        icon={renderIcon(item.icon)}
                        className={classNameItem}
                        key={index}
                    />
                );
            })}
        </div>
    );
};
